.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.listItem {
  position: relative;
  height: 58px;
  padding: 17px 59px 17px 28px;

  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.49px;

  color: #000000;
  text-decoration: none;
}
.title {
  position: absolute;
  top: 2px;
  left: 28px;

  text-transform: uppercase;
  font-family: SackersGothicStd;
  font-size: 9px;
  line-height: 11px;
  text-align: center;
  letter-spacing: 2px;
}
.icon {
  position: absolute;
  right: 27px;
  top: 15px;
}
