.faq {
  width: 100%;
  padding: 0 15px;

  font-family: Montserrat;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}

.faqEntryQuestion {
  width: 100%;
  text-align: left;
  outline: none;
  font-family: inherit;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.49px;

  border: none;
  padding: 0;
  background-color: transparent;
  padding: 15px 0;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.faqCaret {
  display: inline-block;
  transform: rotate(90deg);
}
.faqCaret.expanded {
  transform: rotate(-90deg);
}
.faqEntryAnswer {
  display: none;
  text-align: justify;
}
.faqEntryAnswer.expanded {
  display: block;
}
