.button {
  /* reset */
  border: none;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  outline: none;

  display: inline-block;
  height: 32px;
  width: 110px;
  border-radius: 16px;
  position: relative;

  text-transform: uppercase;
  font-family: SackersGothicStd;
  font-size: 8px;
  text-align: center;
  letter-spacing: 2px;

  color: #000000;
  background: #ffffff;
  border: 1px solid #000000;
  transition: color 0.2s, background-color 0.2s;
}

.checkedLabel,
.uncheckedLabel {
  position: absolute;
  top: 11px;
  left: 12px;
  right: 12px;
  bottom: 13px;
  transition: opacity 0.2s;
}
.checkedLabel {
  opacity: 0;
}

.checked {
  background-color: #000000;
  color: #ffffff;

  & .checkedLabel {
    opacity: 1;
  }
  & .uncheckedLabel {
    opacity: 0;
  }
}
