.button {
  /* reset */
  border: none;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  outline: none;

  padding: 19px 92px;

  text-transform: uppercase;
  font-family: SackersGothicStd;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 2px;

  color: #ffffff;
  background: #000000;

  &.secondary {
    border: 2px solid #e7e7e7;
    color: #2db4e0;
    text-transform: none;
    background-color: transparent;

    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.49px;
    padding: 19px 115px;
  }
}
