.page {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  background: #fff;
}
.backArrow {
  position: absolute;
  top: 8px;
  left: 13px;
  z-index: 2;
}
.title {
  padding-top: 38px;
  padding-bottom: 20px;
  flex-grow: 0;
}
.content {
  flex: 1 1 0;
  overflow: auto;
  position: relative;

  padding-top: 50px;
}

.content.noGap {
  padding-top: 0;
}

/* This CSS is responsible for animations when pages do change */

@value easing: cubic-bezier(0.36, 0.66, 0.04, 1);
@value duration: .5s;
@value rightPosition: translate3d(100%, 0, 0);
@value centerPosition: translate3d(0, 0, 0);
@value leftPosition: translate3d(-33%, 0, 0);
@value hiddenPageOpacity: .4;

:global .transition-wrapper {
  height: 100%;
  position: relative;

  & .transition-item {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: transform duration;
    transition-timing-function: easing;

    /* Low level page is the page that lays deeper in nav than root, gonna slide from right */
    &.lowLevelPage {
      z-index: 2;

      /* Hidden state */
      &.transition-appear,
      &.transition-leave.transition-leave-active {
        transform: rightPosition;
      }

      /* Visible state */
      &.transition-appear.transition-appear-active,
      &.transition-leave {
        transform: centerPosition;
      }
    }

    /* Top level page is root page that is gonna fade to left */
    &.topLevelPage {
      z-index: 1;

      &:after {
        /* fade overlay */
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #000;
        opacity: 0;
        transition: opacity duration;
        transition-timing-function: easing;
      }

      /* Hidden state */
      &.transition-appear,
      &.transition-leave.transition-leave-active {
        transform: leftPosition;
        &:after {
          opacity: hiddenPageOpacity;
        }
      }
      /* Visible state */
      &.transition-appear.transition-appear-active,
      &.transition-leave {
        transform: centerPosition;
        &:after {
          opacity: 0;
        }
      }
    }
  }
}
